export let library = [];
export function generateRespones(v) {
    if (typeof v.text === "string") {
        for (let value of library) {
            if (value.reg) {
                //字符串全字匹配
                let r = new RegExp(value.reg);
                if (r.test(v.text)) {
                    return value.text;
                }
            }
            else if (value.useReg && value.useReg.test(v.text)) {
                //使用自定义匹配
                return value.text;
            }
        }
        return null;
    }
    return null;
}
